IUseCaseExecutor);\nconst studio = container.resolve<IStudio>(IStudio);\nconst tooltip = container.resolve<TooltipImpl>(TooltipImpl);\n\ninterface IImportFile {\n    name: string;\n    content: string; \n    isDuplicate: boolean;\n    type: \"entity\" | \"qjson\" | \"process\" | \"bpmn\";\n}\n\n\n\ninterface INewItemPropsInline extends INewItemProps {\n    parent: IModule | IFolder | IApplication ;\n}\n\ndebugger\nconst props = defineProps<INewItemPropsInline>();\n\nconst fileItems = ref<Array<IImportFile>>([])\n\nconst loadingIcon = ref(false)\nconst emit = defineEmits([\"close\"]);\nconst entityNumber = ref(0);\n\nconst hasEntity = ref(props.parent?.children?.some(i => i.objectType == 'model' && i.modelType == 'entityDesigner') == true);\n\n\n\nfunction checkDuplicate(name: string, type: IImportFile[\"type\"]) {\n    debugger\nconst isDuplicate = props.parent?.children?.some(i => i.name == name.split('.').slice(0, -1).join('.') && (i.objectType == \"model\" && i.modelType == type))\nreturn isDuplicate == true\n\n}\n\nfunction checkExtension(file: string) {\n    const extension = file.split('.')[1]\n    return extension as IImportFile[\"type\"];\n}\n\nasync function selectedFiles() {\n    var item = document.getElementById('fileInput') as HTMLInputElement | null;\n    for (var i = 0; i < item?.files?.length!; i++) {\n        const file = item?.files?.item(i)\n        const isExist = fileItems.value.some(i => i.name == file?.name)\n        if (!isExist) {\n            const text = await file!.text();\n            const extensionOfFile = checkExtension(file!.name)\n            const isDuplicate = checkDuplicate(file!.name, extensionOfFile)\n            fileItems.value.push({ name: file!.name, content: text, isDuplicate, type: extensionOfFile });\n            if (extensionOfFile == \"entity\") {\n                entityNumber.value = entityNumber.value + 1;\n            }\n        }\n\n    }\n    tooltip.startTooltip()\n}\nasync function importSelectedModels() {\n    loadingIcon.value = true;\n    fileItems.value.forEach(async item => {\n        if (item.isDuplicate) { return; }\n\n        const createObject: ICreateModelParam = {\n            name: item.name.split('.').slice(0, -1).join('.'),\n            parentObjectId: props.parent.ID,\n            \n            modelBody: [{ key: item.type, model: item!.content }],\n            modelType: \"qjson\",\n            modelAdditionals: { qjsonType: 'qjson' },\n        }\n\n        switch (item.type) {\n            case 'qjson':\n                createObject.modelType = \"qjson\";\n                createObject.modelAdditionals = { qjsonType: 'qjson' };\n                break;\n            case 'entity':\n                if (isEntityRuleBlock(item)) { return; }\n                createObject.modelType = \"entityDesigner\";\n                createObject.modelAdditionals = {};\n                break;\n            case 'bpmn':\n                createObject.modelType = \"bpmn\";\n                createObject.modelAdditionals = {};\n                break;\n\n            case 'process':\n                createObject.modelType = \"process\";\n                createObject.modelAdditionals = {};\n                break;\n        }\n\n        await executor.executeUseCase<ICreateModel>(ICreateModel, createObject);\n        loadingIcon.value = false;\n        emit('close');\n    });\n\n}\n\nfunction computedIcon(fileType: string) {\n    switch (fileType) {\n        case 'qjson':\n            return 'mdi mdi-palette fileTypeQjson'\n        case 'entity':\n            return 'mdi mdi-database fileTypeEntity'\n        case 'bpmn':\n            return 'mdi mdi-vector-polyline fileTypeBpmn'\n        case 'process':\n            return 'mdi mdi-auto-fix fileTypeProcess'\n        default:\n    }\n}\n\nfunction removeItem(file: IImportFile, selectedFiles: Array<any>) {\n    if (file.type == 'entity') {\n        entityNumber.value = entityNumber.value - 1\n    }\n    const index = selectedFiles.indexOf(file)\n    selectedFiles.splice(index, 1)\n    tooltip.startTooltip()\n}\n\nfunction isEntityRuleBlock(item: IImportFile) {\n    if (item.type != \"entity\") { return false; }\n\n    const isEntityRuleBlock = hasEntity.value || entityNumber.value > 1;\n    return isEntityRuleBlock;\n}\n\nfunction anyNotDuplicateItem() {\n    const retVal = fileItems.value.some(i => i.type != \"entity\" && i.isDuplicate == false);\n    return retVal;\n}\n\n\nconst isButtonDisabled = computed(() => {\n    if (fileItems.value.length == 0) { return true; }\n\n    if (fileItems.value.length > entityNumber.value && anyNotDuplicateItem()) {\n        return false;\n    }\n\n    if (fileItems.value.some(i => i.type != \"entity\")) {\n        return true;\n    }\n\n    const isEntityRuleBlock = entityNumber.value > 1 || (hasEntity.value && entityNumber.value > 0);\n    return isEntityRuleBlock;\n});\n\n</script> \n\n<template>\n    <div class=\"fileInputDiv\">\n        <div class='fileUploadDiv col-12' onclick=\"document.getElementById('fileInput').click()\">\n            <div class='col-12 uploadArea' style=\"display:flex; padding-left: 150px; padding-top: 35px;\">\n                <div class=\"mdi mdi-cloud-upload-outline\"\n                    style=\"text-align: center; font-size: 25px; color:#A1A1A1; padding-right:10px; margin-top: -7px;\">\n                </div>\n                <span style=\"font-weight:bold\">Click</span><span style=\"padding-left:10px\">here to upload your\n                    files.</span>\n            </div>\n        </div>\n        <input style=\"display: none;\" type=\"file\" id=\"fileInput\" multiple @change=\"selectedFiles()\"\n            :accept=\"props.parent.objectType=='application' ? '.qjson':'.qjson,.entity,.bpmn,.process'\">\n\n    </div>\n    <div id=\"selectedFiles\" class=\"selectedItems\">\n\n        <div v-if=\"fileItems.length > 0\">\n            <div class='uploadFilesText'>Selected Files <span> ({{ fileItems.length }})</span></div>\n            <div v-for=\"fileValues in fileItems\">\n                <div v-if=\"!fileValues.isDuplicate && !isEntityRuleBlock(fileValues)\" class='filesValue'>\n                    <div><i :class='computedIcon(fileValues.type)'></i></div>\n                    <div class=\"col-10\" style=\"margin-left: 10px;\">\n                        {{ fileValues.name.split('.').slice(0, -1).join('.') }}\n                    </div>\n\n                    <div class=\"col-1\" style=\"text-align: end; padding-left: 30px;\">\n                        <i v-if=\"fileValues && !loadingIcon\" class='mdi mdi-delete-outline removeIcon'\n                            @click='removeItem(fileValues, fileItems)'></i>\n\n                        <div v-if=\"loadingIcon\" style=\" padding-top: 7px;\" class=\"loader\"></div>\n                    </div>\n\n                </div>\n\n                <div v-if=\"fileValues.isDuplicate || isEntityRuleBlock(fileValues)\" class='filesValue'\n                    style=\"border:1px solid red\">\n                    <div><i :class='computedIcon(fileValues.type)'></i></div>\n                    <div class=\"col-10\" data-toggle=\"tooltip\"\n                        :title=\"isEntityRuleBlock(fileValues) ? 'Only one entity file is allowed' : 'This file is already imported'\"\n                        placement='bottom' style=\"margin-left: 10px;\">\n                        {{ fileValues.name.split('.').slice(0, -1).join('.') }}\n                    </div>\n\n                    <div class=\"col-1\" style=\"text-align: end; padding-left: 30px;\">\n                        <i v-if=\"fileValues && !loadingIcon\" class='mdi mdi-delete-outline removeIcon'\n                            @click='removeItem(fileValues, fileItems)'></i>\n\n                        <div v-if=\"!fileValues.isDuplicate && !isEntityRuleBlock(fileValues) && loadingIcon\"\n                            style=\" padding-top: 7px;\" class=\"loader\"></div>\n                    </div>\n\n                </div>\n\n            </div>\n\n\n        </div>\n    </div>\n    <div class='importBtnDiv'>\n        <button class='cancelBtn' @click=\"emit('close')\">Cancel</button>\n        <button class='importBtn' :disabled=\"isButtonDisabled\"\n            :style=\"{ backgroundColor: isButtonDisabled ? '#37373733' : '#151233' }\"\n            @click=\"importSelectedModels()\">IMPORT</button>\n    </div>\n</template> \n\n<style scoped>\n.fileInputDiv {\n    margin-left: 20px;\n    margin-top: 20px;\n    margin-right: 20px;\n}\n\n.selectedItems {\n    margin-left: 10px;\n    margin-top: 10px;\n    height: 226px;\n    overflow: auto;\n}\n\n.selectedItems::-webkit-scrollbar-thumb {\n    background-color: #D0D1E9;\n    border-radius: 16px;\n}\n\n.selectedItems::-webkit-scrollbar {\n    width: 12px;\n}\n\n.selectedItems::-webkit-scrollbar-track {\n    background: transparent;\n}\n\n.importBtn {\n    border-radius: 4px;\n    border: none;\n    opacity: 1;\n    color: white;\n    width: 97px;\n    height: 40px;\n    font-style: normal;\n    font-size: 20px;\n    font-weight: 500;\n    letter-spacing: 0px;\n    font-family: 'ROBOTO';\n}\n\n.importBtnDiv {\n    text-align: end;\n    margin-right: 20px;\n    margin-top: 30px;\n}\n\n.removeIcon {\n    cursor: pointer;\n    color: #212121;\n    padding-right: 10px;\n    font-style: normal;\n    font-size: 18px;\n    letter-spacing: 0px;\n    opacity: 1;\n}\n\n.loading {\n    cursor: pointer;\n    color: #212121;\n    padding-right: 10px;\n    font-style: normal;\n    font-size: 18px;\n    letter-spacing: 0px;\n    opacity: 1;\n}\n\n::-webkit-file-upload-button {\n    background: #2F3160;\n    color: white;\n    border-radius: 4px;\n    width: 120px;\n    height: 48px;\n}\n\n.fileUploadDiv {\n    cursor: pointer;\n}\n\n.filesValue {\n    margin-top: 10px;\n    background-color: #F5F5FA;\n    color: #000000;\n    display: flex;\n    border-radius: 4px;\n    font-style: normal;\n    font-weight: 400;\n    font-size: 16px;\n    letter-spacing: 0px;\n    opacity: 1;\n    font-family: 'ROBOTO';\n    margin-left: 10px;\n    width: 561px;\n    text-align: start;\n}\n\n.uploadFilesText {\n    color: #616161;\n    font-style: normal;\n    font-weight: 400;\n    font-size: 18px;\n    letter-spacing: 0px;\n    opacity: 1;\n    margin-left: 10px;\n    font-family: \"ROBOTO\";\n}\n\n.fileTypeQjson {\n    color: #449DD1;\n    font-size: 18px;\n    padding-left: 5px;\n}\n\n.fileTypeEntity {\n    color: rgb(233, 114, 76);\n    font-size: 18px;\n    padding-left: 5px;\n}\n\n.fileTypeBpmn {\n    color: rgb(0, 124, 119);\n    font-size: 18px;\n    padding-left: 5px;\n}\n\n.fileTypeProcess {\n    color: rgb(0, 124, 119);\n    font-size: 18px;\n    padding-left: 5px;\n}\n\n.uploadArea {\n    text-align: center;\n    padding-top: 25px;\n    border: 1px dashed #a1a1a1;\n    height: 100px;\n\n}\n\n.cancelBtn {\n    background: none;\n    border: none;\n    font-style: normal;\n    font-weight: 400;\n    font-size: 20px;\n    letter-spacing: 0px;\n    opacity: 1;\n    margin-left: 50px;\n    font-family: \"ROBOTO\";\n    margin-right: 10px;\n    color: #757575;\n    letter-spacing: 0.02px;\n}\n\n.loader {\n    border: 3px solid #f3f3f3;\n    border-radius: 70%;\n    border-top: 3px solid #3498db;\n    width: 12px;\n    height: 12px;\n    -webkit-animation: spin 1s linear infinite;\n    animation: spin 1s linear infinite;\n}\n\n/* Safari */\n@-webkit-keyframes spin {\n    0% {\n        -webkit-transform: rotate(0deg);\n    }\n\n    100% {\n        -webkit-transform: rotate(360deg);\n    }\n}\n\n@keyframes spin {\n    0% {\n        transform: rotate(0deg);\n    }\n\n    100% {\n        transform: rotate(360deg);\n    }\n}\n</style>"],"sourceRoot":""}]);
